<template>
	<div @click="getshow" class="read">
		<div v-if="datashow" class="readheader">
			<span class="readspan" @click="$router.back()">
				<van-icon name="arrow-left" />
			</span>
			<span class="readspan">{{bookInfo.title}}</span>
			<span>
				<van-icon name="search" />
			</span>
		</div>
		<div class="readheaders" v-if="datashow">
			<van-button type="danger" size="small" @click.stop="removeBookrack($route.params.id)" v-if="bookrack.find((id)=>id == $route.params.id)">移除书架</van-button>
			<van-button type="default" size="small" @click.stop="addBookrack($route.params.id)" v-else style="color: ;">加入书架</van-button>
		</div>

		<div class="bottom-controll-panel" v-if="datashow">
			<div class="item clearfix">
				<span class="btn square">默认</span>
				<span class="btn square">夜间</span>
				<span class="btn square">护眼</span>
			</div>
			<div class="item clearfix">
				<span class="btn chapter-btn-w f-l">上一章</span>
				<span class="btn directory" @click.stop="onshow()">目录</span>
				<span class="btn chapter-btn-w f-r">下一章</span>
			</div>
		</div>
		<v-touch v-on:swipeleft="swiperleft" v-on:swiperight="swiperright" class="wrapper">
			<div v-if="content" class="readInside">
				<h1>{{content.title}}</h1>
			
				<div class="readContent">{{content.cpContent}}</div>
			</div>
		</v-touch>
		<chapter v-show="show"></chapter>
	</div>
</template>

<script>
	import {
		mapState,
		mapMutations

	} from "vuex";
	import chapter from '@/views/Chapter.vue';
	export default {
		components:{chapter},
		data() {
			return {
				content: null,
				datashow: false,
				readlist: null,
				link:null,
				isnum:0,
				show:false,
			};
		},
		computed: {
			...mapState(["chapter", "links", "bookInfo", "bookrack"]),
		},
		methods: {
			...mapMutations(['addBookrack', 'removeBookrack']),
			menu: function() {
				window.scrollTo(0, 0);
			},
			getshow() {
				this.datashow = !this.datashow;
			},
			onshow(){
				this.show=!this.show;
			},
			// nextnum:function(){
			// 	if(this.isnum>=this.readlist.length-1){
			// 		Toast("我是有底线的");
			// 		return;
			// 	}
			// 	this.isnum++;
			// }
			swiperleft: function() {
				console.log("左划")
			},
			swiperright: function() {
				console.log("右滑")
			},
		},
		created() {
			this.readlist = this.chapter.chapters;
			this.menu();
			this.link=this.links;
			if (this.chapter) {
				this.axios
					.get(
						"http://novel.kele8.cn/chapters/" +
						encodeURIComponent(this.link ? this.link : this.chapter.chapters[0].link)
					)
					.then((res) => {
						console.log(res.data);
						this.content = res.data.chapter;
					});
			} else {
				console.log("无内容");
			}
		},

	};
</script>

<style lang="less">
	.read {
		.readheader {
			transform: translateY(0);
			transition: transform .15s;
			color: #fff;
			border-bottom: 0;
			background-color: rgba(0, 0, 0, .8);
			position: fixed;
			display: flex;
			width: 100%;
			box-sizing: border-box;
			height: 44px;
			padding-top: 11px;
			padding-bottom: 10px;
			justify-content: space-between;

			.readspan {
				color: #fff;
				font-size: 15px;
				text-align: center;
			}
		}

		.readheaders {
			position: fixed;
			top: 50px;
			right: 0;
			background-color: rgba(0, 0, 0, .5);

			.van-button {
				width: 75px;
				background-color: rgba(0, 0, 0, .5);
				font-size: 10px;
				color: #fff;
				padding: 0px 5px;
				border: 0;
			}
		}

		.bottom-controll-panel {
			transform: translateY(0);
			opacity: 1;
			position: fixed;
			bottom: 0;
			left: 0;
			z-index: 2;
			width: 100%;
			height: 110px;
			padding: 0 20px;
			box-sizing: border-box;
			background-color: rgba(50, 51, 52, .9);
			transition: all .2s ease;

			.item {
				position: relative;
				height: 28px;
				margin-bottom: 19px;
				font-size: 0;

				.btn {
					display: inline-block;
					vertical-align: middle;
					height: 26px;
					line-height: 28px;
					text-align: center;
					font-size: 14px;
				}

				.square {
					width: 94px;
					color: #fff;
					border: 1px solid #535353;
					border-radius: 2px;

					&:nth-of-type(2) {
						margin: 0 16px;
					}
				}

				.chapter-btn-w {
					width: 50px;
					font-size: 14px;
					color: #fff;
				}

				.directory {
					width: 96px;
					height: 28px;
					line-height: 28px;
					color: #a8a8a8;
					text-align: center;
					font-size: 14px;
					position: absolute;
					left: 50%;
					margin-left: -48px;
				}

				.f-l {
					float: left;
				}

				.f-r {
					float: right;
				}
			}

			.clearfix {
				box-sizing: border-box;

				&::before {
					box-sizing: border-box;
					content: '';
					display: table;
				}

				&::after {
					box-sizing: border-box;
					content: '';
					display: table;
					clear: both;
				}
			}
		}
		
		.wrapper{
			touch-action: pan-y !important;
		}
		.readInside {
			padding-top: 30px;

			h1 {
				margin: 0;
				font-size: 23px;
				font-weight: 400;
				color: #333;
				padding: 0 20px;
				line-height: 1.5;
			}

			.readContent {
				white-space: pre-wrap;
				font-size: 16px;
				line-height: 1.5;
				padding: 10px 20px 0;
				text-align: justify;
				color: #5c5d58;
			}
		}
	}
</style>
